import React from "react";
import {graphql} from "gatsby";
import { Link } from "../components";

const Pages = ({ data }) => (
    <div>
      <h1>Pages</h1>
      <ul>
        {data.pages.edges.map(({ node }) => (
          <li key={node.id}>
            <h3>{node.title} - {node.id}</h3>
            <Link primary={true} secondary={undefined} to={ node.path.alias }>{node.title}</Link>
          </li>
        ))}
      </ul>
    </div>
)

export default Pages

export const query = graphql`
  query {
    pages: allNodeAdmissionsFlexiblePage(limit: 1000) {
      edges {
        node {
          title
          id
          path {
            alias
          }
        }
      }
    }
  }
`
